import { localStorageGetData, localStorageRemoveData } from "@/util/localStorageData";
import axios from "axios";
import { removeCookie } from "@/util/cookie";
export default {
  data: function data() {
    return {
      welcomeManager: ""
    };
  },
  mounted: function mounted() {
    this.welcomeManager = localStorageGetData("nowLoginUserCount");
  },
  components: {},
  beforeDestory: function beforeDestory() {
    axios({
      url: "http://localhost:3000/ManagerCount/api/logOut",
      method: "POST",
      data: {
        managerCount: this.welcomeManager
      }
    }).then(function (res) {
      if (res.data.error == 0) {
        localStorageRemoveData("nowLoginUserCount");
        removeCookie("token");
      }
    }).catch(function (err) {
      localStorageRemoveData("nowLoginUserCount");
      removeCookie("token");
    });
  }
};